<p>The use of anti-personnel landmines has been banned by 164 countries, and yet they're still being used in conflicts around the world. There are an estimated 110 million anti-personnel mines still in the ground with more being laid every year.
In 2017, more than 7,000 casualties were caused by mines and other explosive remnants of war, including nearly 2,800 deaths, according to the Landmine Monitor.
More than 120,000 people were killed or injured by landmines between 1999-2017, according to the same group. Nearly half the victims are children, with 84% being boys. Civilians make up 87% of casualties.</p>

